<template>
    <div class="max-w-4xl mx-auto shadow-md p-4  rounded-lg">
        <div class="w-full bg-green-500 p-2 rounded-lg">
            <label for="" class="text-2xl text-white">User List</label>
        </div>
        <div class="mt-4 bg-white">
            <div class="text-left p-4" 
            v-if="ability.includes('User Create') || ability.includes('*') ">
                <router-link to="users/create" class="bg-blue-500 text-white px-6 py-2 rounded-md"> Create </router-link>
            </div>
        
            <table class="table-auto mx-auto w-full text-sm text-left" 
                v-if="ability.includes('User List') || ability.includes('*') ">
                <thead class="text-xs text-gray-700 uppercase bg-gray-50">
                    <tr>
                        <th scope="col" class="px-6 py-3">SL</th>
                        <th scope="col" class="px-6 py-3">Name</th>
                        <th scope="col" class="px-6 py-3">Phone</th>
                        <th scope="col" class="px-6 py-3">Gender</th>
                        <th scope="col" class="px-6 py-3 text-center">Action</th>
                    </tr>
                </thead>
                <tbody>
                    <!-- {{pagination }} -->
                    <tr v-for="(user, index) in user_list.data" :key="user.id" class="bg-white border-b last:border-none">
                        <!-- {{ user }} -->
                        <td scope="row" class="px-4 py-2 font-medium text-gray-900 dark:text-white whitespace-nowrap">{{ ++index }}</td>
                        <td>{{ user.name }}</td>
                        <td>{{ user.phone }}</td>
                        <td>{{ user.gender == 1 ? 'Male': user.gender == 2 ? 'Female':'N/A' }}</td>
                        <td class="flex gap-2 items-center justify-center">
                            <router-link :to="{ name:'UserEdit', params:{ id:user.id }}" class="bg-blue-500 text-white p-1 rounded-md" 
                            v-if="ability.includes('User Edit') || ability.includes('*') ">
                                <svg xmlns="http://www.w3.org/2000/svg" class="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor" stroke-width="2">
                                    <path stroke-linecap="round" stroke-linejoin="round" d="M11 5H6a2 2 0 00-2 2v11a2 2 0 002 2h11a2 2 0 002-2v-5m-1.414-9.414a2 2 0 112.828 2.828L11.828 15H9v-2.828l8.586-8.586z" />
                                </svg>
                            </router-link>
                            <router-link :to="{ name:'UserShow', params:{ id:user.id }}" class="bg-blue-500 text-white p-1 rounded-md" 
                            v-if="ability.includes('User Edit') || ability.includes('*') ">
                                <svg xmlns="http://www.w3.org/2000/svg" class="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor" stroke-width="2">
                                    <path stroke-linecap="round" stroke-linejoin="round" d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
                                    <path stroke-linecap="round" stroke-linejoin="round" d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z" />
                                </svg>
                            </router-link>
                            <div  class="bg-red-500 text-white p-1 rounded-md cursor-pointer" @click="deleteData(user.id)">
                                <svg xmlns="http://www.w3.org/2000/svg" class="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor" stroke-width="2">
                                    <path stroke-linecap="round" stroke-linejoin="round" d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                                </svg>
                            </div>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>
<script>
import Select from "../../../components/select.vue";
import Btn from "../../../components/btn.vue";
import axios, { get_login_token } from "../../../store/axios";
export default {
    components:{
        Select,
        Btn,
        
    },
    data(){
        return {
            page: 1
        }
    },
   
    created(){
        this.$store.dispatch('users/allUser')
    },
    computed:{
        user_list(){
            return this.$store.state.users.all_user || '';
        },
         ability(){
            return this.$store.state.login.abilities || '';
        },
        pagination(){
            return this.user_list.links || ''
        }
    },
    methods:{
        deleteData(data){
             axios().delete(`/api/roles/${data}`)
             .then( () => {
                this.$router.replace({
                    path:'/admin/roles'
                })
             });
        },
        onChangePage(pageOfItems) {
            // update page of items
            this.pageOfItems = pageOfItems;
        }
    }
}
</script>